import React, { useState, useEffect } from 'react';
import Columnchart from './components/Charts/Columnchart.component';
import PieChart from './components/Charts/PieChart.component';

import './App.scss';

function App() {
    const [summaryJson, setSummaryJson] = useState(null);
    
    useEffect(() => {
        const scriptUrl = `https://script.google.com/macros/s/AKfycbzwOqQnn2qBHzyfI2edwCtA5ZZUGKw8ujXBfEdU28zEE0o1dNCp5rH9O5ocErrj6yBngg/exec`;
        fetch(scriptUrl)
            .then(response => response.json())
            .then(answer => {
                setSummaryJson(answer);
               })
            .catch((error) => { console.log(error) });
    }, []);

 // This useEffect will run after summaryJson is updated


   
   
   
   
   useEffect(() => {
    document.title = "teamiff report";
    }, []);
    
    if (! summaryJson) {
        return <div className='loading'>Loading...</div>; // Render a loading state or handle it appropriately
      }

    const  { translated_sections, bad, good, customers_didnt_like, customers_liked,summary,title, abstract, body} = summaryJson;
    const sentences = body.slice(0, body.length - 2).split('.');
    return (
      <div className='App'>


          <div className="logo-container">
              <img
                src="https://static.wixstatic.com/media/ecb741_18c8dd6e99a047d1a90c638766332d9f~mv2.png/v1/fill/w_290,h_92,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/logo%20web%20%202_1x.png"
                alt="Logo"
                className="logo"
              />
          </div>


          <div className='title'>
            <h1>{title}</h1>
            
            <p>{abstract}</p>
          </div>



          <div className='body'>
              {
                sentences.map((element, index) => (
                <span key={index}>{element.trim()+'.'}</span>
                ))
              }
          </div>
          <div className='liked'>
              <div className='customers_liked'>
                <h2>       </h2>
                
                <ol>
                {
                  customers_liked.map( 
                    (element,index) =>
                      {return <li> {String(index+1)+". " + element}</li>})
                }
                </ol>

              </div>

              <div className='customers_didnt_like'>
                    <h2>      </h2>
                    
                    <ul>
                    {
                        customers_didnt_like.map((element, index) => {
                            return <li key={index}>{element}</li>
                        })
                    }
                    <li>sdfsdf</li>
                    </ul>
              </div>

          </div>


          <div className='summary'>
            <p>{summary}</p>
          </div>

          <div className='charts_container'>
              
            <div className='good_sentiment chart'>
              <Columnchart 
              color = {'#1DBE3A'}
              title={"   "}
              data = {good}
              />
            </div >

            <div className='bad_sentiment chart'>
              <Columnchart 
                color = {'#ff4411'}
                title = {"   "}
                data = {bad}
                />
            </div>

            <div className='freq_topic chart'>
              <PieChart
                color = {'#ff4411'}
                title = {"   "}
                data = {bad}
                />
            </div>  
          </div>

      </div>
    );
}

export default App;
