import React from 'react';
import CanvasJSReact from '@canvasjs/react-charts';
import './chart.style.scss';
const PieChart = ({color,title,data}) => {
    const datapoints = [];
    
    if (data){
        data.topics.forEach((element,index) => {
            datapoints.push({label:element,y:data.counts[index]})
        });
        
    }

    const options = {
        animationEnabled: true,
        exportEnabled: true,
        theme: "light1",
        
         // "light1", "dark1", "dark2"
        title:{
            text: title,
            fontSize: 18, // Correct property name and place it directly inside `title`
            fontFamily: "Calibri", // Example font family, you can use any
            fontWeight: "bold", // Example font weight
            fontStyle: "normal", // Example font style
            horizontalAlign: "center" // Center align the title
        },
        axisY: {
            title: "Count",
            includeZero: true,
            titleFontSize: 14,
        },
        axisX: {
            title: "",
            reversed: true,
            titleFontSize: 14,
        },
        data: [
            {
                type: "pie",
                 indexLabel: "{label}:  (#percent%)",
                 
                toolTipContent: "{label}: {y} (#percent%)",
                dataPoints: datapoints
            }
        ],
        
    };

    return (

    <div style={{ position: 'relative' }}>
        <CanvasJSReact.CanvasJSChart options = {options}
            /* onRef={ref => this.chart = ref} */
        />
        <div className="custom-credit" >
          <a href="https://www.teamiff.com" target="_blank" rel="noopener noreferrer">Teamiff Ltd.</a>
        </div>
      </div>
        
    );
}

export default PieChart;
